import React from 'react';
import './AboutMe.scss';
import Fade from 'react-reveal/Fade';

function AboutMe() {
    return (
        <article id='about__me' className='about__me'>
            <h1>Get to know me</h1>
            <Fade delay={400} duration={1000}>
                <div className='aboutMe__picture'></div>
            </Fade>
            <div className='aboutMe__paragraphs'>
                <Fade delay={200} duration={500}>
                    <p className='aboutMe__paragraph'>
                        Hello, I am Hubert Łepski, Junior Front-End Developer
                        living in Wrocław - I create websites with dedication
                        and joy.
                    </p>
                </Fade>

                <Fade delay={400} duration={700}>
                    <p className='aboutMe__paragraph'>
                        I am really passionate about web development and design,
                        so I continually learn new things and improve my skills.
                    </p>
                </Fade>

                <Fade casdade delay={600} duration={900}>
                    <p className='aboutMe__paragraph'>
                        I am an open person and I prefer working in a group even
                        more than working alone. I like to discuss other
                        people's ideas and suggest my own solutions.
                    </p>
                </Fade>

                <Fade casdade delay={700} duration={1000}>
                    <p className='aboutMe__paragraph'>
                        Apart from web development I enjoy spending my free time
                        with friends, mainly by playing football and some video
                        games or hanging out.
                    </p>
                </Fade>
            </div>
        </article>
    );
}

export default AboutMe;
