import React, { useMemo } from 'react';
import './Map.scss';
import { GoogleMap, useLoadScript, MarkerF } from '@react-google-maps/api';

function Map() {
    const { isLoaded } = useLoadScript({
        googleMapsAPiKey: 'AIzaSyBgu9iySP8lOu8cfGBH7vWBaHufdMZz-RM',
    });

    const position = useMemo(() => ({ lat: 51.105, lng: 17.034 }), []);
    const markerPosition = useMemo(() => ({ lat: 51.097, lng: 17.034 }), []);

    return (
        isLoaded && (
            <section className='section__map' id='map'>
                <GoogleMap
                    zoom={13}
                    center={position}
                    mapContainerClassName='map'
                    mapTypeId='hybrid'
                    clickableIcons={false}
                    draggable={false}
                >
                    <MarkerF position={markerPosition} opacity={0.8} />
                </GoogleMap>
            </section>
        )
    );
}

export default Map;
