import React, { useState, useCallback } from 'react';
import './Introduction.scss';
import Fade from 'react-reveal/Fade';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBarsStaggered, faClose } from '@fortawesome/free-solid-svg-icons';

function Introduction() {
    const [open, setOpen] = useState(false);

    const outsideModalClick = useCallback((e) => {
        if (
            e.target.matches('#menu__icon') ||
            !e.target.closest('.introduction__menu')
        ) {
            setOpen(false); //zamknięcie modalu po kliknięciu poza niego
        }
    }, []);

    document.addEventListener('click', outsideModalClick);

    const closeModal = useCallback(() => {
        document.querySelector('.content').classList.add('fixNav');
        !open &&
            window.scrollTo({
                top: 0,
                left: 0,
            });
        setTimeout(() => {
            document.querySelector('.content').classList.remove('fixNav');
        }, 5); // usuwa klasę po 5ms

        setTimeout(() => {
            setOpen(!open);
        }, 800); // modal zamyka się po .8s
    }, [open]);

    return (
        <Fade left big>
            <section className='introduction'>
                <nav className='introduction__menu'>
                    <menu
                        className={`introduction__modal ${
                            open ? 'activeStyle' : ''
                        }`}
                    >
                        <li>
                            <a href='#about__me' onClick={() => closeModal()}>
                                About Me
                            </a>
                        </li>
                        <li>
                            <a href='#skills' onClick={() => closeModal()}>
                                Skills
                            </a>
                        </li>
                        <li>
                            <a href='#projects' onClick={() => closeModal()}>
                                Projects
                            </a>
                        </li>
                        <li>
                            <a href='#contact' onClick={() => closeModal()}>
                                Contact
                            </a>
                        </li>
                    </menu>
                    <span id='menu__icon'>
                        <FontAwesomeIcon
                            onClick={() => setOpen(!open)}
                            className={`introduction__icon ${
                                open ? 'activeStyle' : ''
                            }`}
                            icon={open ? faClose : faBarsStaggered}
                        />
                    </span>
                </nav>
                {/*  menu wyświetlające się na mobilce */}
                <nav className='mobile__menu'>
                    <li>
                        <a href='#about__me' onClick={() => closeModal()}>
                            About Me
                        </a>
                    </li>
                    <li>
                        <a href='#skills' onClick={() => closeModal()}>
                            Skills
                        </a>
                    </li>
                    <li>
                        <a href='#projects' onClick={() => closeModal()}>
                            Projects
                        </a>
                    </li>
                    <li>
                        <a href='#contact' onClick={() => closeModal()}>
                            Contact
                        </a>
                    </li>
                </nav>

                <div
                    className={`introduction__info ${
                        open ? 'activeStyle' : ''
                    }`}
                >
                    <p>Hello, I am Hubert Łepski</p>
                    <p>Junior Front-End Developer</p>
                    <a href='#contact'>
                        <button>Let's work together!</button>
                    </a>
                </div>
            </section>
        </Fade>
    );
}

export default Introduction;
