import React from 'react';
import Slider from 'react-slick';
import './Projects.scss';

function Projects() {
    const settings = {
        autoplay: true,
        autoplaySpeed: 2500,
        slidesToShow: 2,
        slidesToScroll: 1,
        pauseOnHover: true,
        pauseOnFocus: true,
        focusOnSelect: true,
        accessibility: true,
        centerMode: false,
        arrows: false,
        dots: false,
        infinite: true,
        responsive: [
            {
                breakpoint: 1340,
                settings: {
                    slidesToShow: 1,
                    dots: true,
                },
            },
        ],
    };
    return (
        <article id='projects' className='projects'>
            <h1>PROJECTS</h1>
            <Slider {...settings} className='projects__slider'>
                <a
                    href='https://inspirer10.github.io/weather-app/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/project1.jpg' alt='Weather App' />
                    <p className='project__info'>Weather App</p>
                </a>

                <a
                    href='https://inspirer10.github.io/webstore/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/webstore.jpg' alt='Webstore' />
                    <p className='project__info'>Webstore</p>
                </a>

                <a
                    href='https://inspirer10.github.io/tuscany/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/tuscany.jpg' alt='Tuscany' />
                    <p className='project__info'>Tuscany</p>
                </a>

                <a
                    href='https://inspirer10.github.io/TicTacToe/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/tictactoe.jpg' alt='Tic Tac Toe' />
                    <p className='project__info'>Tic Tac Toe</p>
                </a>

                <a
                    href='https://inspirer10.github.io/hangman-react/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/hangman.jpg' alt='Hangman' />
                    <p className='project__info'>Hangman</p>
                </a>

                <a
                    href='https://inspirer10.github.io/restaurant-page/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/restaurant.png' alt='Restaurant Page' />
                    <p className='project__info'>Restaurant Page</p>
                </a>

                <a
                    href='https://inspirer10.github.io/currencyConverter/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img
                        src='img/currencyConverter.jpg'
                        alt='Currency converter'
                    />
                    <p className='project__info'>Currency Converter</p>
                </a>

                <a
                    href='https://inspirer10.github.io/trvl-stadiums-page/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/TRVLstadiums.png' alt='TRVLstadiums' />
                    <p className='project__info'>TRVL Stadiums</p>
                </a>

                <a
                    href='https://inspirer10.github.io/oneArmed-Bandit/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/oneArmed.png' alt='One Armed Bandit' />
                    <p className='project__info'>One Armed Bandit</p>
                </a>

                <a
                    href='https://inspirer10.github.io/simpleToDoList/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/toDo.png' alt='To Do List' />
                    <p className='project__info'>To Do List</p>
                </a>

                <a
                    href='https://inspirer10.github.io/papier-kamien-nozyce/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/PRS.png' alt='Paper Rocks Scissors' />
                    <p className='project__info'>Paper Rock Scissors</p>
                </a>

                <a
                    href='https://inspirer10.github.io/marledipawele/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/marle.png' alt='Marle Di Pawele' />
                    <p className='project__info'>Marle Di Pawele</p>
                </a>

                <a
                    href='https://inspirer10.github.io/przelicznik-temperatur/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/temperature.png' alt='Temp Converter' />
                    <p className='project__info'>Temp Converter</p>
                </a>

                <a
                    href='https://inspirer10.github.io/feed-india/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/feedIndia.jpg' alt='FeedIndia' />
                    <p className='project__info'>FeedIndia</p>
                </a>

                <a
                    href='https://inspirer10.github.io/manhattan/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/manhattan.jpg' alt='Manhattan' />
                    <p className='project__info'>Manhattan</p>
                </a>

                <a
                    href='https://inspirer10.github.io/kalkulator-BMI/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <img src='img/bmi.png' alt='BMI Calculator' />
                    <p className='project__info'>BMI Calculator</p>
                </a>
            </Slider>
        </article>
    );
}

export default Projects;
