import React from 'react';
import Mailer from './Mailer';
import './Contact.scss';
import {
    BsFillTelephoneOutboundFill,
    BsLinkedin,
    BsGithub,
    BsDownload,
} from 'react-icons/bs';
import { GoMailRead } from 'react-icons/go';

function Contact() {
    return (
        <article id='contact' className='contact'>
            <h1>CONTACT ME</h1>
            <section className='contact__section'>
                <Mailer />

                <div className='contact__links'>
                    <div className='contact__item'>
                        <p className='contact__heading'>Contact</p>
                        <p>hubertlepsky@gmail.com</p>
                    </div>
                    <div className='contact__item'>
                        <a href='#map'>
                            <p className='contact__heading'>Based in</p>
                            <p> Wrocław, Poland </p>
                        </a>
                    </div>
                    <div className='contact__item'>
                        <p className='contact__heading'>Phone</p>
                        <p>(+48) 697 819 866</p>
                    </div>
                </div>
            </section>

            <a
                className='download__cv'
                href='CV_HŁ.pdf'
                download='Hubert Łepski CV.pdf'
                target='_blank'
                rel='noopener noreferrer'
            >
                <button className='cv__btn'>
                    Download CV <BsDownload className='icon' />
                </button>
            </a>

            <div className='contact__icons'>
                <a href='tel:+48697819866'>
                    <BsFillTelephoneOutboundFill className='icon' />
                </a>
                <a href='mailto:hubertlepsky@gmail.com'>
                    <GoMailRead className='icon' />
                </a>
                <a
                    href='https://www.linkedin.com/in/hubert-%C5%82epski-64751b16a/'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <BsLinkedin className='icon' />
                </a>
                <a
                    href='https://github.com/inspirer10'
                    target='_blank'
                    rel='noopener noreferrer'
                >
                    <BsGithub className='icon' />
                </a>
            </div>
        </article>
    );
}

export default Contact;
